import { useState } from "react";
import { LoginFormData, UseLoginReturn } from "../types/login";

export const useLogin = (
  onLogin?: (formData: LoginFormData) => Promise<void>,
  onForgotPassword?: (email: string) => Promise<void>,
  error?: string
): UseLoginReturn => {
  const [formData, setFormData] = useState<LoginFormData>({
    email: "",
    password: "",
    rememberMe: false,
  });
  const [localError, setLocalError] = useState<string>("");

  const handleInputChange = (e: React.ChangeEvent<HTMLInputElement>) => {
    const { name, value, type, checked } = e.target;
    setFormData((prev) => ({
      ...prev,
      [name]: type === "checkbox" ? checked : value,
    }));
    // Clear errors when user starts typing
    if (localError) setLocalError("");
  };

  const handleSubmit = async (e: React.FormEvent) => {
    e.preventDefault();
    setLocalError("");

    // Basic validation
    if (!formData.email) {
      setLocalError("Email is required");
      return;
    }
    if (!formData.password) {
      setLocalError("Password is required");
      return;
    }

    try {
      await onLogin?.(formData);
    } catch (err) {
      setLocalError(err instanceof Error ? err.message : "Login failed");
    }
  };

  const handleForgotPassword = async () => {
    if (!formData.email) {
      setLocalError("Please enter your email first");
      return;
    }

    try {
      await onForgotPassword?.(formData.email);
    } catch (err) {
      setLocalError(
        err instanceof Error ? err.message : "Failed to send reset email"
      );
    }
  };

  const displayError = error || localError;

  return {
    formData,
    localError,
    displayError,
    handleInputChange,
    handleSubmit,
    handleForgotPassword,
  };
};

// API utility functions
export const loginAPI = {
  authenticate: async (credentials: LoginFormData) => {
    const response = await fetch("/api/auth/login", {
      method: "POST",
      headers: { "Content-Type": "application/json" },
      body: JSON.stringify({
        email: credentials.email,
        password: credentials.password,
        rememberMe: credentials.rememberMe,
      }),
    });

    if (!response.ok) {
      const errorData = await response.json();
      throw new Error(errorData.message || "Authentication failed");
    }

    return response.json();
  },

  resetPassword: async (email: string) => {
    const response = await fetch("/api/auth/forgot-password", {
      method: "POST",
      headers: { "Content-Type": "application/json" },
      body: JSON.stringify({ email }),
    });

    if (!response.ok) {
      const errorData = await response.json();
      throw new Error(errorData.message || "Password reset failed");
    }

    return response.json();
  },
};

// Validation utilities
export const validateEmail = (email: string): boolean => {
  const emailRegex = /^[^\s@]+@[^\s@]+\.[^\s@]+$/;
  return emailRegex.test(email);
};

export const validatePassword = (password: string): boolean => {
  return password.length >= 8;
};

export const validateLoginForm = (formData: LoginFormData): string | null => {
  if (!formData.email.trim()) {
    return "Email is required";
  }

  if (!validateEmail(formData.email)) {
    return "Please enter a valid email address";
  }

  if (!formData.password) {
    return "Password is required";
  }

  if (!validatePassword(formData.password)) {
    return "Password must be at least 8 characters long";
  }

  return null;
};

// Reusable Login Handler Configuration
export interface LoginConfig {
  apiUrl: string;
  method?: "POST" | "PUT";
  headers?: Record<string, string>;
  onSuccess?: (data: any) => void | Promise<void>;
  onError?: (error: string) => void;
  transformRequest?: (formData: LoginFormData) => any;
  transformResponse?: (response: any) => any;
}

// Generic Reusable Login Handler
export const createLoginHandler = (config: LoginConfig) => {
  return async (formData: LoginFormData): Promise<void> => {
    try {
      // Transform request if needed
      const requestBody = config.transformRequest
        ? config.transformRequest(formData)
        : {
            email: formData.email,
            password: formData.password,
            rememberMe: formData.rememberMe,
          };

      const response = await fetch(config.apiUrl, {
        method: config.method || "POST",
        headers: {
          "Content-Type": "application/json",
          ...config.headers,
        },
        body: JSON.stringify(requestBody),
      });

      if (!response.ok) {
        const errorData = await response.json().catch(() => ({}));
        const errorMessage = errorData.message || `HTTP ${response.status}: ${response.statusText}`;
        throw new Error(errorMessage);
      }

      let responseData = await response.json();

      // Transform response if needed
      if (config.transformResponse) {
        responseData = config.transformResponse(responseData);
      }

      // Handle success
      if (config.onSuccess) {
        await config.onSuccess(responseData);
      }
    } catch (error) {
      const errorMessage = error instanceof Error ? error.message : "Login failed";
      
      if (config.onError) {
        config.onError(errorMessage);
      }
      
      throw new Error(errorMessage);
    }
  };
};

// Pre-configured Login Handlers for Common APIs
export const loginHandlers = {
  // Standard REST API
  standard: (apiUrl: string) =>
    createLoginHandler({
      apiUrl,
      onSuccess: (data) => {
        console.log("Login successful:", data);
        // Store token, redirect, etc.
      },
    }),

  // API with custom headers (e.g., API key)
  withApiKey: (apiUrl: string, apiKey: string) =>
    createLoginHandler({
      apiUrl,
      headers: {
        "X-API-Key": apiKey,
      },
    }),

  // Laravel/PHP API format
  laravel: (apiUrl: string) =>
    createLoginHandler({
      apiUrl,
      transformRequest: (formData) => ({
        email: formData.email,
        password: formData.password,
        remember: formData.rememberMe,
      }),
      onSuccess: (data) => {
        if (data.token) {
          localStorage.setItem("auth_token", data.token);
          localStorage.setItem("user", JSON.stringify(data.user));
        }
      },
    }),

  // .NET Core API format
  dotnet: (apiUrl: string) =>
    createLoginHandler({
      apiUrl,
      transformRequest: (formData) => ({
        Email: formData.email,
        Password: formData.password,
        RememberMe: formData.rememberMe,
      }),
      onSuccess: (data) => {
        if (data.accessToken) {
          localStorage.setItem("access_token", data.accessToken);
          localStorage.setItem("refresh_token", data.refreshToken);
        }
      },
    }),

  // Firebase/Custom format
  firebase: (apiUrl: string) =>
    createLoginHandler({
      apiUrl,
      transformRequest: (formData) => ({
        email: formData.email,
        password: formData.password,
        returnSecureToken: true,
      }),
      transformResponse: (response) => ({
        token: response.idToken,
        user: {
          id: response.localId,
          email: response.email,
        },
      }),
    }),
};
